.history_item {
  display: flex;
  align-items: center;
  width: 100%;
  padding: 15px 0;
  border-bottom: 1px solid #f7f8ff;
}

.history_item_img {
  flex-basis: 9%;
  img {
    border-radius: 50%;
  }
}

.history_item_descr {
  font-size: 16px;
  color: #232323;
  flex-basis: 18%;
  padding-left: 15px;
  .history_item_code {
    font-weight: 500;
    color: #ee4a65;
    margin-bottom: 5px;
  }
}

.history_item_text {
  flex-basis: 31%;
  padding-right: 15px;
}

.history_item_coints {
  font-size: 16px;
  text-align: center;
  font-weight: 500;
  flex-basis: 10%;
  padding-right: 15px;
}

.history_item_price {
  font-size: 20px;
  font-weight: 500;
  flex-basis: 16%;
}

.history_item_btn {
  flex-basis: 12%;
  .history_item_b {
    display: flex;
    height: 50px;
    width: 100%;
    align-items: center;
    justify-content: center;
    border-radius: 5px;
    font-size: 16px;
    font-weight: 600;
    &.history_item_b_green {
      color: #2bc155;
      background-color: #e9f9ee;
    }
    &.history_item_b_yealow {
      color: #ffb800;
      background-color: #fff8e5;
    }
    &.history_item_b_red {
      color: #ff0000;
      background-color: #ffe5e5;
    }
  }
}

.history_item_dots {
  flex-basis: 4%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  span {
    border-radius: 50%;
    width: 4px;
    height: 4px;
    display: inline-block;
    background-color: #000;
    margin-bottom: 3px;
    &:last-child {
      margin: 0;
    }
  }
}

.history_item_yeallow {
  color: #f0941c;
  font-size: 14px;
  margin-bottom: 10px;
  font-weight: 500;
}

.history_item_span {
  font-size: 16px;
  color: #676767;
}

.history_item_date,
.history_item_time {
  display: flex;
  align-items: center;
  img {
    display: inline-block;
    margin-right: 8px;
  }
}

.history_item_blue {
  font-size: 14px;
  color: #707bcc;
  font-weight: 500;
}

.history_item_green {
  font-weight: 500;
  font-size: 14px;
  color: #50af95;
}

.history_row {
  margin-bottom: 70px;
}

.history_pagin_row {
  margin-bottom: 20px;
  .voice_pagination {
    margin-right: 0;
    ul {
      justify-content: center;
      span,a {
        font-size: 18px;
        font-weight: 500;
        min-width: 45px;
        height: 45px;
        background-color: #ededed;
        &:hover {
          background-color: #ea4989;
        }
      }
      .arrow_li {
        span,a {
          background-color: transparent;
          &:hover {
            background-color: transparent;
            opacity: 0.7;
          }
        }
      }
    }
  }
  .voice_pagination ul li.active span,a {
    background-color: #ea4989;
  }
}

.li_start {
  transform: rotate(180deg);
}
.filter_history {
  position: relative;
  .select_filter {
    padding-left: 70px;
    background-color: transparent;
    padding-right: 40px;
  }
  .filter_history_form {
    background-image: url('../../../images/history_select.png');
    background-repeat: no-repeat;
    background-position: 15% center;
  }
}
.select_filter {
  position: relative;
  min-width: 130px;
  height: 50px;
  font-size: 14px;
  font-weight: 500;
  border-radius: 50px;
  border: 1px solid #f4f5f9;
  cursor: pointer;
  padding-left: 15px;
  padding-right: 20px;
  background-image: url('../../../images/chevr_pink.png');
  background-repeat: no-repeat;
  background-position: 87% 49%;
}

select {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}