.pride_title{
  text-align: center;
  font-size: 42px;
  font-weight: 700;
  text-transform: uppercase;
  position: relative;
  padding-bottom: 30px;
  margin-bottom: 35px;
  letter-spacing: 6px;
  padding-top: 37px;
  &:after{
    content:"";
    width: 90px;
    opacity: 0.8;
    height: 1px;
    position: absolute;
    bottom:0;
    left:calc(50% - 45px);
    background: #be62ad;
  }
}
.pride_subtitle{
  line-height: 1.5;
  font-weight: 300;
  text-align: center;
  text-transform: uppercase;
  position: relative;
  z-index: 6;
}

.bodyHome{
  line-height: 1.25;
  font-size: 16px;
  background-color: #6324ad;
  color: #fff;
  overflow-x: hidden;
  position: relative;
  height: 100%;
}

a{
  cursor: pointer;
}
body *{
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  font-style: normal;
  outline: none !important;
}
ol, ul {
  list-style: none;

}

blockquote, q {
  quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
.containerP{
  max-width: 1190px;
  width: 98%;
  margin-left: auto;
  margin-right: auto;
}
.first_containerP{
  max-width: 1490px;
  width: 98%;
  margin-left: auto;
  margin-right: auto;
}


img{
  max-width: 100%;
  height: auto;
}
a, a:hover, a:focus, a:visited, button{
  outline: none;
  text-decoration: none;
  color: inherit;
  transition: all 0.3s ease-in-out;
}
h1, .first_title{
  font-size: 76px;
  font-weight: 700;
  line-height: 1.2;
  span{
    font-weight: 700;
    color: #01d2e1;
  }
}
.inputp {
  color: #fff;
  padding: 20px 25px;
  margin-bottom: 10px;
  border: 0;
  background: #401979;
  border-radius: 5px;
  resize: none;
  display: block;
  &::placeholder {
    color: #ada2c3;
  }
}
.subm_form {
  display: flex;
  align-items: center;
  text-transform: uppercase;
  padding: 20px;
  border: 0;
  margin-right: auto;
  margin-left: auto;
  position: relative;
  font-size: 13px;
  margin-top: 20px;
  border-radius: 5px;
  transition: all 0.3s ease-in-out;
  background: rgb(231, 51, 81);
  background: linear-gradient(90deg, rgba(231, 51, 81, 1) 0%, rgba(231, 51, 81, 1) 0%, #de00ab 100%);
  &:hover {
    color: #fec40c;
    background: #de00ab;
    background: linear-gradient(90deg, #de00ab 0%, #de00ab 0%, rgba(231, 51, 81, 1) 100%);
  }

  &:after {
    padding-left: 8px;
    content: "";
    font-family: "FontAwesome";
    font-size: 13px;
  }
}
.breadcrumbs {
  margin-bottom: 15px;
  ul {
    display: flex;
    align-items: center;
    position: relative;
    &:before {
      height: 1px;
      content: "";
      width: 30px;
      margin-right: 10px;
      display: inline-block;
      background: #fdb50a;
    }
    li {
      text-transform: uppercase;
      color: #fdb50a;
      font-size: 17px;
      a {
        cursor: pointer;
        display: flex;
        align-items: center;
        font-size: inherit;
        &:after {
          height: 1px;
          content: "";
          width: 10px;
          margin: 0 5px;
          display: inline-block;
          background: #fdb50a;
        }
        &:hover {
          color: #e94c51;
        }
      }
    }
  }
}
.header_simple {
  margin-bottom: 40px;
  margin-top: 35px;
  .containerP {
    flex-wrap: wrap;
    display: flex;
    justify-content: space-between;
    &:after {
      content: "";
      display: block;
      width: 100%;
      border-top: 1px solid #2d0135;
      border-bottom: 1px solid rgba(255, 255, 255, 0.3);
    }
  }
  .simple_left {
    margin-top: 130px;
    flex-basis: 62%;
