@keyframes shake {
  0% { transform: translate(-49%, -49%) rotate(0deg); }
  10% { transform: translate(-51%, -52%) rotate(-1deg); }
  20% { transform: translate(-53%, -50%) rotate(1deg); }
  30% { transform: translate(-47%, -48%) rotate(0deg); }
  40% { transform: translate(-51%, -49%) rotate(1deg); }
  50% { transform: translate(-51%, -48%) rotate(-1deg); }
  60% { transform: translate(-53%, -49%) rotate(0deg); }
  70% { transform: translate(-47%, -51%) rotate(-1deg); }
  80% { transform: translate(-49%, -49%) rotate(1deg); }
  90% { transform: translate(-51%, -52%) rotate(0deg); }
  100% { transform: translate(-51%, -48%) rotate(-1deg); }
}

.captcha_wrapper{
  border-radius: 5px;
  border: none!important;
  width: initial!important;
  box-shadow: 9px 12px 8px 0px rgba(34, 60, 80, 0.2);
  padding: 20px!important;
}
.captcha_fail{
  animation: shake 0.5s;
  animation-iteration-count: infinite;
}