.timer_r {
  -webkit-box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.35);
  -moz-box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.35);
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.35);
  position: fixed;
  display: flex;
  top:50%;
  right: 15px;
  z-index: 10;
  align-items: center;
  border-radius: 5px;
  background: #a377dd;
  background: linear-gradient(0deg, #a377dd 0%, #a377dd 0%, #be76df 100%);
  .pres {
    padding: 10px;
    border-radius: 5px;
    background: #e73351;
    background: linear-gradient(90deg, #e73351 0%, #e73351 0%, #ea5450 100%);
    .toform {
      width: 45px;
      height: 45px;
      display: flex;
      justify-content: center;
      align-items: center;
      text-transform: uppercase;
      font-weight: 500;
      font-size: 13px;
      transition: all 0.3s ease-in-out;
      border-radius: 50%;
      background: #181818;
      cursor: pointer;
      background: linear-gradient(90deg, #181818 0%, #181818 0%, #2e2e2e 100%);
      &:hover {
        background: #a377dd;
        background: linear-gradient(0deg, #a377dd 0%, #a377dd 0%, #be76df 100%);
      }
    }
  }
  .timer_right {
    display: flex;
    align-items: center;
    flex-direction: column;
    align-items: center;
    min-width: 115px;
    text-align: center;
    .timer_date {
      font-size: 30px;
      font-weight: 700;
      text-shadow: 1px 1px 2px #888;
    }
    .timer_price {
      letter-spacing: 1.2px;
      font-size: 16px;
      color: #d8ff00;
      text-shadow: 1px 1px 2px #ccc;
    }
  }
}
